
.Home-item-icon {
  background-image: url("./images/icons@2x.png");
  background-size: 192px 224px;
  width: 30px;
  height: 30px;
  display: inline-block;
}
.Home-item-icon-keysignature {
  background-position: 0px 0;
}
.Home-item-icon-note {
  background-position: -32px 0;
}
.Home-item-icon-interval{
  background-position: -64px 0;
}
.Home-item-icon-scale {
  background-position: -96px 0;
}
.Home-item-icon-chord {
  background-position: -128px 0;
}

a {
  text-decoration: none;
  /*color: #3083fb;*/
  color: white;
}

.Home-button {
  margin: 8px;
  float: left;
  width: 25px;
  height: 25px;
  line-height:56px; 
  color: blue;
  text-align: center;
  /*background: #e0e0e0;*/
  border-radius: 50%;
  border: 1px solid white;
}

.Home-button-hi {
  width: 13px;
  height: 13px;
  background: white;
  margin: 6px;
  border-radius: 50%;
  /*box-shadow: 0 0 5px #909090;*/
}
.Home-icon {
  width: 160px;
  height: 80px;
  line-height: 80px;
  padding: 2px;
  float: left;
  text-align: center;
  vertical-align: middle;
/*  border-color: gray;
  border-style: solid;
  border-width: 1px 0.5px 1px 0.5px;
*/
  border-style: solid;
  border-color: rgba(0,0,0,0.8);
  border-width: 1px;
  /*background: linear-gradient(0deg, #1452a1, #369bdf);*/
  background: -webkit-gradient(linear, left bottom, left top, from(#337ac3), color-stop(49%, #4d8fca), color-stop(51%, #5e9fd6), color-stop(95%, #8bb5e0), to(#c7e2f8));
  background: -webkit-linear-gradient(bottom, #337ac3, #4d8fca 49%, #5e9fd6 51%, #8bb5e0 95%, #c7e2f8);
  background: -o-linear-gradient(bottom, #337ac3, #4d8fca 49%, #5e9fd6 51%, #8bb5e0 95%, #c7e2f8);
  background: linear-gradient(0deg, #337ac3, #4d8fca 49%, #5e9fd6 51%, #8bb5e0 95%, #c7e2f8);
  margin: 10px 24px 0px 24px; /*top, right, bottom, left*/
  border-radius: 12px;
}
.Home-icon-hi {
  /*box-shadow: 0 0 5px #3083fb;*/
  /*box-shadow: 0 0 5px #909090;*/
  /*background: linear-gradient(0deg, #e15b01, #ffba8c);*/
  background: -webkit-gradient(linear, left bottom, left top, from(#d9762e), color-stop(49%, #ca8b56), color-stop(51%, #d9945a), color-stop(95%, #d9be9e), to(#e9d2bf));
  background: -webkit-linear-gradient(bottom, #d9762e, #ca8b56 49%, #d9945a 51%, #d9be9e 95%, #e9d2bf);
  background: -o-linear-gradient(bottom, #d9762e, #ca8b56 49%, #d9945a 51%, #d9be9e 95%, #e9d2bf);
  background: linear-gradient(0deg, #d9762e, #ca8b56 49%, #d9945a 51%, #d9be9e 95%, #e9d2bf);
  /*background: rgba(255,255,255,0.4);*/
}



