.App {
  /*text-align: center;*/
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@font-face {  
    font-family: 'AloisenNew';
    src: url('Aloisen New.woff'); 
}
@font-face {
  font-family: 'iconfont music';
  src: url('iconfont_music.woff') format('woff');
}
@font-face {
  font-family: 'iconfontCommon';
  src: url('iconfont_common.woff') format('woff');
}
.iconfont{
  font-family:"iconfont music" !important;
  font-size:32px;font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}

.iconfontcommon{
  font-family:"iconfontCommon" !important;
  font-size:16px;font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}

.App-musfont {
  font-family: 'AloisenNew';
/*  font-size: 1.3em;*/
}
.with-musfont-big {
  font-family: 'AloisenNew';
  /*height: 48px;*/
  line-height: 48px;
  margin-left: 20px;
}
.with-musfont-small {
  font-family: 'AloisenNew';
  /*height: 32px;*/
  line-height: 24px;
}

.App-line {
  width: 99%; 
  height: 1px; 
  /*margin-left: 40px;*/
  margin-right: 80px;
  /*margin-bottom: 10px;*/
/*  margin-top: 20px;*/
  background: black;
}
table {
  border-collapse:collapse;
}
th, td{
  border-bottom: solid 1px rgba(255,255,255,0.5);
  height: 72px;
  text-align: center;
}

.White-line {
  background: -webkit-gradient(linear, left top, right top, from(rgba(255,255,255,1)), to(rgba(200,200,200,0)));
  background: -webkit-linear-gradient(left, rgba(255,255,255,1), rgba(200,200,200,0));
  background: -o-linear-gradient(left, rgba(255,255,255,1), rgba(200,200,200,0));
  background: linear-gradient(90deg, rgba(255,255,255,1), rgba(200,200,200,0));
  height: 1px;
  margin-left: 10px;
  margin-top: 2px;
  margin-bottom: 8px;
}



